import React, { useState, useEffect } from 'react';
import { 
  Users, 
  FileText, 
  CheckCircle, 
  Clock,
  TrendingUp,
  AlertCircle
} from 'lucide-react';
import { 
  PieChart, 
  Pie, 
  Cell, 
  ResponsiveContainer,
  BarChart,
  Bar,
  XAxis,
  YAxis,
  Tooltip
} from 'recharts';

const Dashboard = ({ stats }) => {
  const [checklist, setChecklist] = useState([]);

  useEffect(() => {
    fetchChecklist();
  }, []);

  const fetchChecklist = async () => {
    try {
      const response = await fetch('/api/dashboard/checklist/carlos-palacios-construction');
      const data = await response.json();
      setChecklist(data);
    } catch (error) {
      console.error('Error fetching checklist:', error);
    }
  };

  if (!stats) return <div className="loading">Loading dashboard...</div>;

  const complianceData = [
    { name: 'Complete', value: stats.documents.generated, color: '#10b981' },
    { name: 'Pending', value: stats.documents.required - stats.documents.generated, color: '#6366f1' }
  ];

  const activityData = [
    { name: 'Documents', value: stats.documents.generated },
    { name: 'Signatures', value: stats.signatures.signed },
    { name: 'Training', value: stats.training.completions }
  ];

  return (
    <div>
      <div className="page-header">
        <h1>Dashboard</h1>
        <p>Welcome back! Here's your compliance overview.</p>
      </div>

      <div className="stats-grid">
        <div className="stat-card">
          <div className="stat-header">
            <span className="stat-title">Total Employees</span>
            <div className="stat-icon">
              <Users size={24} />
            </div>
          </div>
          <div className="stat-value">{stats.employees.total}</div>
          <div className="stat-change">
            <TrendingUp size={16} />
            {stats.employees.active} active
          </div>
        </div>

        <div className="stat-card">
          <div className="stat-header">
            <span className="stat-title">Documents</span>
            <div className="stat-icon">
              <FileText size={24} />
            </div>
          </div>
          <div className="stat-value">{stats.documents.generated}/{stats.documents.required}</div>
          <div className="stat-change">
            {stats.documents.complianceRate}% complete
          </div>
        </div>

        <div className="stat-card">
          <div className="stat-header">
            <span className="stat-title">Signatures</span>
            <div className="stat-icon">
              <CheckCircle size={24} />
            </div>
          </div>
          <div className="stat-value">{stats.signatures.rate}%</div>
          <div className="stat-change">
            {stats.signatures.signed} of {stats.signatures.required} signed
          </div>
        </div>

        <div className="stat-card">
          <div className="stat-header">
            <span className="stat-title">Pending</span>
            <div className="stat-icon">
              <Clock size={24} />
            </div>
          </div>
          <div className="stat-value">{stats.pendingSignatures}</div>
          <div className="stat-change" style={{ color: '#f59e0b' }}>
            <AlertCircle size={16} />
            Action needed
          </div>
        </div>
      </div>

      <div style={{ display: 'grid', gridTemplateColumns: '1fr 1fr', gap: '24px' }}>
        <div className="card">
          <div className="card-header">
            <span className="card-title">Compliance Overview</span>
          </div>
          <div style={{ height: '250px' }}>
            <ResponsiveContainer width="100%" height="100%">
              <PieChart>
                <Pie
                  data={complianceData}
                  cx="50%"
                  cy="50%"
                  innerRadius={60}
                  outerRadius={80}
                  paddingAngle={5}
                  dataKey="value"
                >
                  {complianceData.map((entry, index) => (
                    <Cell key={`cell-${index}`} fill={entry.color} />
                  ))}
                </Pie>
                <Tooltip />
              </PieChart>
            </ResponsiveContainer>
          </div>
          <div style={{ textAlign: 'center', marginTop: '16px' }}>
            <span style={{ fontSize: '24px', fontWeight: '700', color: '#10b981' }}>
              {stats.documents.complianceRate}%
            </span>
            <p style={{ color: '#94a3b8', fontSize: '14px' }}>Compliance Rate</p>
          </div>
        </div>

        <div className="card">
          <div className="card-header">
            <span className="card-title">Activity</span>
          </div>
          <div style={{ height: '250px' }}>
            <ResponsiveContainer width="100%" height="100%">
              <BarChart data={activityData}>
                <XAxis dataKey="name" stroke="#94a3b8" />
                <YAxis stroke="#94a3b8" />
                <Tooltip 
                  contentStyle={{ 
                    backgroundColor: '#1e293b', 
                    border: '1px solid #334155',
                    borderRadius: '8px'
                  }}
                />
                <Bar dataKey="value" fill="#6366f1" radius={[8, 8, 0, 0]} />
              </BarChart>
            </ResponsiveContainer>
          </div>
        </div>
      </div>

      <div className="card" style={{ marginTop: '24px' }}>
        <div className="card-header">
          <span className="card-title">Compliance Checklist</span>
        </div>
        <div>
          {checklist.map((item) => (
            <div key={item.documentType} style={{ 
              display: 'flex', 
              alignItems: 'center', 
              gap: '16px',
              padding: '16px',
              borderBottom: '1px solid #334155'
            }}>
              <div style={{ 
                width: '40px', 
                height: '40px', 
                borderRadius: '50%',
                background: item.generated ? 'rgba(16, 185, 129, 0.2)' : 'rgba(99, 102, 241, 0.2)',
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
                color: item.generated ? '#10b981' : '#6366f1'
              }}>
                {item.generated ? <CheckCircle size={20} /> : <Clock size={20} />}
              </div>
              <div style={{ flex: 1 }}>
                <div style={{ fontWeight: '600', color: '#f1f5f9' }}>{item.name}</div>
                <div style={{ fontSize: '13px', color: '#94a3b8' }}>
                  {item.generated ? 'Generated' : 'Not Generated'}
                </div>
              </div>
              <span style={{ 
                padding: '6px 12px', 
                borderRadius: '20px',
                fontSize: '12px',
                fontWeight: '500',
                background: item.generated ? 'rgba(16, 185, 129, 0.2)' : 'rgba(245, 158, 11, 0.2)',
                color: item.generated ? '#10b981' : '#f59e0b'
              }}>
                {item.generated ? 'Complete' : 'Pending'}
              </span>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

export default Dashboard;
