import React, { useState, useEffect } from 'react';
import { Users, Plus, Mail, Phone, Briefcase } from 'lucide-react';

const Employees = () => {
  const [employees, setEmployees] = useState([]);

  useEffect(() => {
    loadEmployees();
  }, []);

  const loadEmployees = async () => {
    try {
      const response = await fetch('/api/company/carlos-palacios-construction/employees');
      const data = await response.json();
      setEmployees(data);
    } catch (error) {
      console.error('Error loading employees:', error);
    }
  };

  return (
    <div>
      <div className="page-header">
        <h1>Employees</h1>
        <p>Manage your team and compliance status</p>
      </div>

      <div style={{ marginBottom: '24px' }}>
        <button className="btn btn-primary">
          <Plus size={16} /> Add Employee
        </button>
      </div>

      <div style={{ display: 'grid', gridTemplateColumns: 'repeat(2, 1fr)', gap: '24px' }}>
        {employees.map((emp) => (
          <div key={emp.id} className="card">
            <div style={{ display: 'flex', alignItems: 'center', gap: '16px' }}>
              <div style={{ 
                width: '56px', 
                height: '56px', 
                borderRadius: '50%',
                background: 'linear-gradient(135deg, #6366f1, #8b5cf6)',
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
                fontSize: '20px',
                fontWeight: '700',
                color: 'white'
              }}>
                {emp.firstName[0]}{emp.lastName[0]}
              </div>
              <div style={{ flex: 1 }}>
                <div style={{ fontSize: '18px', fontWeight: '600', color: '#f1f5f9' }}>{emp.firstName} {emp.lastName}</div>
                <div style={{ display: 'flex', alignItems: 'center', gap: '8px', color: '#94a3b8', fontSize: '14px', marginTop: '4px' }}>
                  <Briefcase size={14} /> {emp.position}
                </div>
              </div>
              <span style={{ 
                padding: '6px 12px',
                borderRadius: '20px',
                fontSize: '12px',
                fontWeight: '500',
                background: 'rgba(16, 185, 129, 0.2)',
                color: '#10b981'
              }}>
                {emp.status}
              </span>
            </div>

            <div style={{ marginTop: '16px', paddingTop: '16px', borderTop: '1px solid #334155' }}>
              <div style={{ display: 'flex', flexDirection: 'column', gap: '8px' }}>
                <div style={{ display: 'flex', alignItems: 'center', gap: '8px', fontSize: '14px', color: '#94a3b8' }}>
                  <Mail size={14} /> {emp.email}
                </div>
                {emp.phone && (
                  <div style={{ display: 'flex', alignItems: 'center', gap: '8px', fontSize: '14px', color: '#94a3b8' }}>
                    <Phone size={14} /> {emp.phone}
                  </div>
                )}
                <div style={{ fontSize: '14px', color: '#94a3b8' }}>Department: {emp.department}</div>
              </div>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default Employees;
