import React from 'react';
import { Video, Play, CheckCircle } from 'lucide-react';

const Training = () => {
  const modules = [
    { title: 'Anti-Harassment Training', duration: '30 min', completed: true },
    { title: 'Construction Safety', duration: '45 min', completed: false },
    { title: 'Wage & Hour Compliance', duration: '20 min', completed: false },
    { title: 'Timekeeping Procedures', duration: '15 min', completed: false }
  ];

  return (
    <div>
      <div className="page-header">
        <h1>Training Center</h1>
        <p>Employee training and compliance courses</p>
      </div>

      <div style={{ display: 'grid', gridTemplateColumns: 'repeat(2, 1fr)', gap: '24px' }}>
        {modules.map((module, idx) => (
          <div key={idx} className="card">
            <div style={{ display: 'flex', alignItems: 'center', gap: '16px', marginBottom: '16px' }}>
              <div style={{ 
                width: '48px', 
                height: '48px', 
                background: module.completed 
                  ? 'rgba(16, 185, 129, 0.2)' 
                  : 'linear-gradient(135deg, rgba(99, 102, 241, 0.2), rgba(139, 92, 246, 0.2))',
                borderRadius: '12px',
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
                color: module.completed ? '#10b981' : '#6366f1'
              }}>
                {module.completed ? <CheckCircle size={24} /> : <Video size={24} />}
              </div>
              <div>
                <div style={{ fontSize: '16px', fontWeight: '600', color: '#f1f5f9' }}>{module.title}</div>
                <div style={{ fontSize: '13px', color: '#94a3b8' }}>{module.duration}</div>
              </div>
            </div>

            <button className="btn btn-primary" style={{ width: '100%', justifyContent: 'center' }}>
              <Play size={16} /> {module.completed ? 'Review' : 'Start Training'}
            </button>
          </div>
        ))}
      </div>
    </div>
  );
};

export default Training;
