import React, { useState, useEffect } from 'react';
import { Routes, Route, NavLink } from 'react-router-dom';
import { 
  LayoutDashboard, 
  FileText, 
  PenTool, 
  Video, 
  Users, 
  Settings,
  Bell,
  Search,
  Menu,
  X
} from 'lucide-react';
import Dashboard from './pages/Dashboard';
import Documents from './pages/Documents';
import Signatures from './pages/Signatures';
import Training from './pages/Training';
import Employees from './pages/Employees';

function App() {
  const [loading, setLoading] = useState(true);
  const [sidebarOpen, setSidebarOpen] = useState(true);
  const [stats, setStats] = useState(null);

  useEffect(() => {
    fetchStats();
  }, []);

  const fetchStats = async () => {
    try {
      const response = await fetch('/api/dashboard/stats/carlos-palacios-construction');
      const data = await response.json();
      setStats(data);
      setLoading(false);
    } catch (error) {
      console.error('Error fetching stats:', error);
      setLoading(false);
    }
  };

  if (loading) {
    return (
      <div className="loading">
        <div className="logo-icon">HR</div>
        <span>Loading...</span>
      </div>
    );
  }

  return (
    <div className="app">
      <nav className="sidebar">
        <div className="sidebar-header">
          <div className="logo">
            <div className="logo-icon">HR</div>
            <div className="logo-text">
              <span className="logo-title">Compliance</span>
              <span className="logo-subtitle">Agent Pro</span>
            </div>
          </div>
        </div>
        
        <ul className="nav-menu">
          <li>
            <NavLink to="/" className={({ isActive }) => isActive ? 'nav-link active' : 'nav-link'} end>
              <LayoutDashboard size={20} />
              <span>Dashboard</span>
            </NavLink>
          </li>
          <li>
            <NavLink to="/documents" className={({ isActive }) => isActive ? 'nav-link active' : 'nav-link'}>
              <FileText size={20} />
              <span>Documents</span>
            </NavLink>
          </li>
          <li>
            <NavLink to="/signatures" className={({ isActive }) => isActive ? 'nav-link active' : 'nav-link'}>
              <PenTool size={20} />
              <span>E-Signatures</span>
            </NavLink>
          </li>
          <li>
            <NavLink to="/training" className={({ isActive }) => isActive ? 'nav-link active' : 'nav-link'}>
              <Video size={20} />
              <span>Training</span>
            </NavLink>
          </li>
          <li>
            <NavLink to="/employees" className={({ isActive }) => isActive ? 'nav-link active' : 'nav-link'}>
              <Users size={20} />
              <span>Employees</span>
            </NavLink>
          </li>
        </ul>
        
        <div className="sidebar-footer">
          <div className="company-info">
            <span className="company-name">Carlos Palacios Construction</span>
            <span className="company-status"> Active</span>
          </div>
        </div>
      </nav>
      
      <main className="main-content">
        <Routes>
          <Route path="/" element={<Dashboard stats={stats} />} />
          <Route path="/documents" element={<Documents />} />
          <Route path="/signatures" element={<Signatures />} />
          <Route path="/training" element={<Training />} />
          <Route path="/employees" element={<Employees />} />
        </Routes>
      </main>
    </div>
  );
}

export default App;
