import React, { useState, useEffect } from 'react';
import { FileText, Download, RefreshCw, CheckCircle, Clock } from 'lucide-react';

const Documents = () => {
  const [documents, setDocuments] = useState([]);
  const [generating, setGenerating] = useState(null);

  const documentTypes = [
    { type: 'employee-handbook', name: 'Employee Handbook', description: 'Complete employee handbook with company policies' },
    { type: 'wage-hour-policy', name: 'Wage and Hour Policy', description: 'California wage and hour compliance policy' },
    { type: 'timekeeping-breaks-policy', name: 'Timekeeping and Breaks Policy', description: 'Meal and rest period tracking requirements' },
    { type: 'anti-harassment-policy', name: 'Anti-Harassment Policy', description: 'Zero-tolerance harassment and discrimination policy' },
    { type: 'owner-compensation-memo', name: 'Owner Compensation Memo', description: 'IRS-compliant owner compensation documentation' },
    { type: 'owner-adoption-resolution', name: 'Owner Adoption Resolution', description: 'Board resolution adopting all policies' }
  ];

  useEffect(() => {
    loadDocuments();
  }, []);

  const loadDocuments = async () => {
    try {
      const response = await fetch('/api/documents/carlos-palacios-construction');
      const data = await response.json();
      setDocuments(data);
    } catch (error) {
      console.error('Error loading documents:', error);
    }
  };

  const handleGenerate = async (documentType) => {
    setGenerating(documentType);
    try {
      await fetch('/api/documents/carlos-palacios-construction/generate', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ documentType })
      });
      await loadDocuments();
    } catch (error) {
      console.error('Error generating document:', error);
    } finally {
      setGenerating(null);
    }
  };

  const getDocumentStatus = (type) => {
    const doc = documents.find(d => d.documentType === type);
    return doc ? 'generated' : 'pending';
  };

  return (
    <div>
      <div className="page-header">
        <h1>Documents</h1>
        <p>Generate and manage compliance documents</p>
      </div>

      <div style={{ display: 'grid', gridTemplateColumns: 'repeat(3, 1fr)', gap: '24px' }}>
        {documentTypes.map((doc) => {
          const status = getDocumentStatus(doc.type);
          
          return (
            <div key={doc.type} className="card">
              <div style={{ display: 'flex', alignItems: 'flex-start', gap: '16px', marginBottom: '16px' }}>
                <div style={{ 
                  width: '48px', 
                  height: '48px', 
                  background: 'linear-gradient(135deg, rgba(99, 102, 241, 0.2), rgba(139, 92, 246, 0.2))',
                  borderRadius: '12px',
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'center',
                  color: '#6366f1'
                }}>
                  <FileText size={24} />
                </div>
                <div style={{ flex: 1 }}>
                  <h3 style={{ fontSize: '16px', fontWeight: '600', color: '#f1f5f9', marginBottom: '4px' }}>{doc.name}</h3>
                  <p style={{ fontSize: '13px', color: '#94a3b8', lineHeight: '1.5' }}>{doc.description}</p>
                </div>
              </div>

              <div style={{ marginBottom: '16px' }}>
                <span style={{ 
                  display: 'inline-flex',
                  alignItems: 'center',
                  gap: '6px',
                  padding: '6px 12px',
                  borderRadius: '20px',
                  fontSize: '12px',
                  fontWeight: '500',
                  background: status === 'generated' ? 'rgba(16, 185, 129, 0.2)' : 'rgba(245, 158, 11, 0.2)',
                  color: status === 'generated' ? '#10b981' : '#f59e0b'
                }}>
                  {status === 'generated' ? <><CheckCircle size={14} /> Generated</> : <><Clock size={14} /> Not Generated</>}
                </span>
              </div>

              <button
                className="btn btn-primary"
                onClick={() => handleGenerate(doc.type)}
                disabled={generating === doc.type}
                style={{ width: '100%', justifyContent: 'center' }}
              >
                {generating === doc.type ? (
                  <><RefreshCw size={16} className="spin" /> Generating...</>
                ) : status === 'generated' ? (
                  <><RefreshCw size={16} /> Regenerate</>
                ) : (
                  <><FileText size={16} /> Generate Document</>
                )}
              </button>
            </div>
          );
        })}
      </div>
    </div>
  );
};

export default Documents;
